a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,canvas,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video{margin:0;padding:0;border:0;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{width:100wh;height:90vh;color:#fff;background:linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);background-size:400% 400%;animation:Gradient 15s ease infinite}@keyframes Gradient{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}#canvas{position:absolute;top:50%;left:50%;transform:translate3d(-50%, -50%, 0) rotate(0deg)}.btn{cursor:pointer;position:absolute;width:150px;height:150px;top:50%;left:50%;transform:translate3d(-50%, -50%, 0) rotate(0deg);background-repeat:no-repeat;background-position:center;background-size:150px;opacity:.3;transition:.3s all}.btn:hover{opacity:1}.play{background-image:url("../img/play.png")}.pause{background-image:url("../img/pause.png")}
